.serviceOfferings {
  padding: 0 0 rem(72px);
  &__wrapper {
    background-color: $color-secondary-gray;
    border-radius: $radius-md;
    padding: rem(76px) 0 rem(46px);
    .container {
      max-width: rem(1410px) !important; } }

  h2 {
    text-align: center; }

  &__list {
    margin: rem(72px) rem(-11.5px) 0;

    .slick-slide {
      margin: 0 rem(11.5px); } }

  &__listItem {
    padding: rem(42px) rem(52px) rem(49px);
    background-color: $color-white;
    border-radius: $radius-md;

    h5 {}

    p {
      margin-top: rem(10px);
      line-height: 1.25; } } }

@include max-screen($breakpoint-sm) {
  .serviceOfferings {
    padding: 0 0 rem(38px);
    &__wrapper {
      padding: rem(42px) 0 rem(38px); }

    &__list {
      margin: rem(32px) rem(-11.5px) 0; }
    &__listItem {
      padding: rem(42px) rem(22px) rem(49px);
      h5 {}

      p {
        font-size: rem(11px); } } } }
