#app {
	min-height: calc(100vh - 460px);
	box-sizing: border-box;
	overflow: hidden;
	@include max-screen(769px) {
		min-height: 0; } }
#main {
	width: 100%;
	overflow: hidden;
	padding-top: rem(80px);
	@include max-screen($breakpoint-xl);
	@include max-screen($breakpoint-lg) {
		padding-top: rem(80px); }
	@include max-screen($breakpoint-sm) {
		padding-top: rem(80px); } }


.divider {
	display: block;
	height: 1px;
	background: #cfd0d2; }

.app-container--y--middle {
	display: flex;
	flex-direction: column;
	justify-content: center; }

.no-scroll {
	overflow: hidden;
	height: 100vh; }

