html, body {
  -webkit-overflow-scrolling: touch; }

body {
	font-family: $font-family-base;
	font-size: 16px;
	font-weight: 400;
	font-style: normal;
	line-height: $default-line-height;
	color: $default-text-color;
	-webkit-font-smoothing: antialiased {
  	-moz-osx-font-smoothing: grayscale;
  	-webkit-backface-visibility: hidden;
  	-webkit-tap-highlight-color: transparent; } }

h1,h2,h3,h4,h5,h6,
.h1,.h2,.h3,.h4,.h5,.h6 {
	font-weight: 600;
	line-height: 1.1em;
	font-family: $font-family-title; }

h1, .h1 {
	font-size: rem(60px);
	@include max-screen($breakpoint-lg) {
		font-size: rem(40px); }

	@include max-screen($breakpoint-sm) {
		font-size: rem(24px); } }
h2, .h2 {
	font-size: rem(50px);

	@include max-screen($breakpoint-lg) {
		font-size: rem(35px); }

	@include max-screen($breakpoint-sm) {
		font-size: rem(22px); } }

h3, .h3 {
	font-size: rem(35px);
	line-height: 1.2;
	@include max-screen($breakpoint-lg) {
		font-size: rem(30px); }

	@include max-screen($breakpoint-sm) {
		font-size: rem(18px); } }

h4, .h4 {
	font-size: rem(30px);
	line-height: 1.2;
	@include max-screen($breakpoint-lg) {
		font-size: rem(25px); }
	@include max-screen($breakpoint-sm) {
		font-size: rem(18px); } }

h5, .h5 {
	font-size: rem(25px);
	@include max-screen($breakpoint-sm) {
		font-size: rem(17px); } }

h6, .h6 {
	font-size: rem(20px); }

p,.p {
	font-size: $text-font-size;
	@include max-screen($breakpoint-lg);

	@include max-screen($breakpoint-md); }

.text {
	&--style {}
	&--size--10,&--size--10 * {
		font-size: rem(10px);
		line-height: 1.2em;
		@include max-screen($breakpoint-lg);
		@include max-screen($breakpoint-md) {
			font-size: rem(10px); } }
	&--size--12,&--size--12 * {
		font-size: rem(12px);
		line-height: 1.2em;
		@include max-screen($breakpoint-lg);
		@include max-screen($breakpoint-md) {
			font-size: rem(12px); } }
	&--size--13,&--size--13 * {
		font-size: rem(13px);
		line-height: 1.2em;
		@include max-screen($breakpoint-lg);
		@include max-screen($breakpoint-md) {
			font-size: rem(13px); } }
	&--size--14,&--size--14 * {
		font-size: rem(14px);
		line-height: 1.2em;
		@include max-screen($breakpoint-lg);
		@include max-screen($breakpoint-md) {
			font-size: rem(14px); } }
	&--size--15,&--size--15 * {
		font-size: rem(15px);
		line-height: 1.2em;
		@include max-screen($breakpoint-lg);
		@include max-screen($breakpoint-md) {
			font-size: rem(15px); } }
	&--size--16,&--size--16 * {
		font-size: rem(16px);
		line-height: 1.2em;
		@include max-screen($breakpoint-lg);
		@include max-screen($breakpoint-md) {
			font-size: rem(14px); } }
	&--size--17,&--size--17 * {
		font-size: rem(17px);
		line-height: 1.2em;
		@include max-screen($breakpoint-lg);
		@include max-screen($breakpoint-md) {
			font-size: rem(14px); } }
	&--size--18,&--size--18 * {
		font-size: rem(18px);
		line-height: 1.2em;
		@include max-screen($breakpoint-lg);
		@include max-screen($breakpoint-md) {
			font-size: rem(18px); } }
	&--size--20,&--size--20 * {
		font-size: rem(20px);
		line-height: 1.2em;
		@include max-screen($breakpoint-lg);
		@include max-screen($breakpoint-md) {
			font-size: rem(20px); } }

	&--size--22,&--size--22 * {
		font-size: rem(22.5px);
		line-height: 1.2em;
		@include max-screen($breakpoint-lg);
		@include max-screen($breakpoint-md) {
			font-size: rem(22.5px); } }

	&--default,&--default * {
		font-size: rem(16px);
		line-height: 1.2em; }
	&--uppercase {
		text-transform: uppercase; }
	&--left {
		text-align: left; }
	&--right {
		text-align: right; }
	&--center {
		text-align: center; }
	&--color {
		&--white {
			&, & * {
				color: $color-white; } }

		&--light-gray {
			&, & * {
				color: $color-light-gray; } }

		&--gray {
			&, & * {
				color: $color-gray; } }

		&--dark-gray {
			&, & * {
				color: $color-dark-gray; } }

		&--black {
			&, & * {
				color: $color-black; } }

		&--red {
			&, & * {
				color: $color-red; } }

		&--dark-red {
			&, & * {
				color: $color-dark-red; } }

		&--bright-red {
			&, & * {
				color: $color-bright-red; } }

		&--green {
			&, & * {
				color: $color-green; } }

		&--light-mint {
			&, & * {
				color: $color-light-mint; } }

		&--neutral-gray {
			&, & * {
				color: $color-neutral-gray; } }

		&--border-gray {
			&, & * {
				color: $color-border-gray; } }

		&--secondary-gray {
			&, & * {
				color: $color-secondary-gray; } }

		&--dark-black {
			&, & * {
				color: $color-dark-black; } } }

	&--underline {
			text-decoration: underline; } }


.font {
	&--italic {
		font-style: italic; }
	&--weight {
		&--thin {
			font-weight: 100; }
		&--light {
			font-weight: 300; }
		&--regular {
			font-weight: 400; }
		&--medium {
			font-weight: 600; }
		&--bold {
			font-weight: 800; }
		&--black {
			font-weight: 900; } } }


.content-block {
	p {
		margin-bottom: 20px;
		&:nth-child(1) {
			padding-top: 0; }
		&:nth-last-child(1) {
			padding-bottom: 0; } }
	h1,h2,h3,h4,h5,h6 {
		margin-bottom: rem(15px);
		line-height: 1.2em; }
	h1 {
		margin-bottom: rem(20px); }

	p,ul,ol {
		font-size: rem(16px);
		line-height: rem(28px); }
	a {
		overflow-wrap: break-word;
		word-wrap: break-word;
		transition: 0.3s;
		color: $color-red;
		&:hover {
			opacity: 0.8;
			text-decoration: underline; } }
	ul {
		margin: rem(15px 0);
		margin-left: rem(30px);
		li {
			display: block;
			//padding-left: 25px
			margin-bottom: 10px;
			line-height: rem(34px);
			&:nth-last-child(1) {
				margin-bottom: 0; }
			&:before {
				content: '';
				background-color: $color-black;
				width: 5px;
				height: 5px;
				min-width: 5px;
				border-radius: 50%;
				margin-right: 10px;
				margin-bottom: 0.12em;
				display: inline-block; }
			ol {
				margin: rem(10px 0); }
			ul {
				margin: rem(10px 0);
				margin-left: rem(30px); } } }
	ol {
		list-style: none;
		counter-reset: my-counter;
		margin: rem(15px 0);
		li {
			counter-increment: my-counter;
			padding-left: 25px;
			margin-bottom: 10px;
			&:nth-last-child(1) {
				margin-bottom: 0; }
			&:before {
				content: counter(my-counter) ".";
				color: $color-black;
				margin-right: 10px;
				margin-top: 8px; }
			ol {
				margin: rem(10px 0); }
			ul {
				margin: rem(10px 0);
				margin-left: rem(30px); } } }
	img {
		width: auto;
		height: auto; }
	code {
		margin: 11px 0;
		line-height: $default-line-height;
		padding: 2px 4px;
		border: 1px solid #dde4e9;
		background-color: #F6F6F6;
		color: #232527;
		display: inline-block; }
	pre {
		white-space: pre-wrap; }
	blockquote {
		margin: rem(25px 0);
		position: relative;
		border-left: 2px solid $color-black;
		padding-left: 30px;
		p {
			font-size: rem(20px);
			line-height: rem(31px);
			font-weight: 400;
			&:nth-last-child(1) {
				font-size: rem(16px);
				font-weight: 300; } }
		@include max-screen($breakpoint-md);

		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: -20px;
			width: 60px;
			height: 51px;
			//background-image: url(../assets/images/quote.svg)
			background-repeat: no-repeat;
			background-size: contain;
			background-position: center;
			z-index: -1; } } }

.bg--color {
	&--white {
		background-color: $color-white; }

	&--light-gray {
		background-color: $color-light-gray; }

	&--gray {
		background-color: $color-gray; }

	&--dark-gray {
		background-color: $color-dark-gray; }

	&--black {
		background-color: $color-black; }

	&--red {
		background-color: $color-red; }

	&--dark-red {
		background-color: $color-dark-red; }

	&--bright-red {
		background-color: $color-bright-red; }

	&--green {
		background-color: $color-green; }

	&--light-mint {
		background-color: $color-light-mint; }

	&--neutral-gray {
		background-color: $color-neutral-gray; }

	&--border-gray {
		background-color: $color-border-gray; }

	&--secondary-gray {
		background-color: $color-secondary-gray; }

	&--dark-black {
		background-color: $color-dark-black; } }
