@keyframes pulseAnimation {
	0% {
		opacity: 0; }
	50% {
		opacity: 1; }
	100% {
		opacity: 0; } }

.lazy-pulse {
	position: relative;
	&:before,&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		z-index: 5;
		width: 100%;
		height: 100%;
		background-color: #efefef;
		transition: 0.3s linear; }
	&:after {
		background-color: #e2e2e2;
		animation: pulseAnimation 2s infinite; }
	&.loaded {
		&:before, &:after {
			opacity: 0;
			animation: none; }
		&:after {
			display: none; }
		&:after {
			content: '';
			display: block;
			background:#FF4A6A {}
			opacity: 0;
			position: absolute;
			width: 100%;
			height: 100%;
			transition: 0.3s;
			z-index: 1; }
		&:hover:after {
			opacity: 0.2; } } }


.lazy-img {
	img {} }
